  <v-container fluid>

    <v-toolbar flat color="white">
      <v-toolbar-title class="green--text">Members</v-toolbar-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <v-spacer></v-spacer>
      <v-btn color="green lighten-1" dark class="mb-2">Export Data</v-btn>
    </v-toolbar>


    <v-toolbar flat color="white">
      <v-toolbar-items>
        <v-layout align-center justify-space-between row fill-height>
          <v-flex grow xs3>
            <v-text-field
              :value="reservation.bookingId"
              label="Booking ID"
              readonly
            ></v-text-field>
          </v-flex>
          <v-flex grow xs3>
            <v-text-field
              :value="reservation.guestName"
              label="Guest Name"
              readonly
            ></v-text-field>
          </v-flex>
          <v-flex grow xs3>
            <v-text-field
              :value="reservation.memberCount"
              label="Number of Members"
              readonly
            ></v-text-field>
          </v-flex>
          <v-flex grow xs3>
            <v-text-field
              :value="reservation.phoneNo"
              label="Phone Number"
              readonly
            ></v-text-field>
          </v-flex>
          <v-flex grow xs3>
            <v-text-field
              :value="reservation.checkInDate"
              label="Check-in On"
              readonly
            ></v-text-field>
          </v-flex>
          <v-flex grow xs3>
            <v-text-field
              :value="reservation.checkOutDate"
              label="Check-out On"
              readonly
            ></v-text-field>
          </v-flex>
        </v-layout>
      </v-toolbar-items>
    </v-toolbar>

    <v-toolbar
      slot="header"
      class="mt-2"
      color="green lighten-1"
      dark
      dense
      flat
    >
      <v-toolbar-title class="subheading">ID Pictures</v-toolbar-title>
    </v-toolbar>

    <amplify-s3-album :path="idPath" :s3AlbumConfig="imageOption"></amplify-s3-album>
    <v-toolbar
      slot="header"
      class="mt-2"
      color="green lighten-1"
      dark
      dense
      flat
    >
      <v-toolbar-title class="subheading">Member Pictures</v-toolbar-title>
    </v-toolbar>
    
    <amplify-s3-album :path="facePath" :s3AlbumConfig="imageOption"></amplify-s3-album>
    <v-toolbar
      slot="header"
      class="mt-2"
      color="green lighten-1"
      dark
      dense
      flat
    >
      <v-toolbar-title class="subheading">Member Information</v-toolbar-title>
    </v-toolbar>
    <v-data-iterator
      :items="members"
      :pagination.sync="pagination"
      content-tag="v-layout"
      hide-actions
      row
      wrap
    >

      <v-flex
        slot="item"
        slot-scope="props"
        xs6
        sm4
        md3
        lg2
      >
        <v-card>
          <v-card-title class="subheading font-weight-bold">Member No. {{ reservation.bookingId }}-{{ props.item.memberNo }}</v-card-title>

          <v-divider></v-divider>

          <v-list dense>
            <v-list-tile>
              <v-list-tile-content>Full Name: {{ props.item.fullName }}</v-list-tile-content>
